<template>
  <v-app id="FaceID">


 <v-app-bar
      absolute
      color="#fcb69f"
      dark

      src="https://www.aurecongroup.com/-/media/images/aurecon/content/expertise/digital-engineering-advisory/machine-learning/aurecon-machine-learning-large-banner.jpg?w=1170&h=417&as=1&crop=1"
  
    >
      <template v-slot:img="{ props }">
        <v-img
          v-bind="props"
          gradient="to top right, rgba(19,84,122,.5), rgba(128,208,199,.8)"
        ></v-img>
      </template>
  

      <v-toolbar-title>{{switch1 == true ? ' REGISTER':'LOGIN'}}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn @click=" switch1 = !switch1" >
          <span>{{switch1 != true ? ' REGISTER':'LOGIN'}}</span>
        <v-icon>account_circle</v-icon>
       
      </v-btn>
    </v-app-bar>
   
    <v-content  id="login">
       <v-row
          align="center"
          justify="center"
         
        >
          <v-col
            cols="12"
            sm="8"
            md="4"
            
          >
            <v-card class="elevation-3">
              
              <v-toolbar
              id="toolbar"
              >
                  <v-switch
                v-model="switch1"
               
             style="margin-top:1.5em;"
                ></v-switch>
                <v-toolbar-title style="margin-left: 35%; color:white;" >
                  
{{switch1 == true ? ' REGISTER':'LOGIN'}}
                </v-toolbar-title>
  
              </v-toolbar>
              <v-card-text >
                <v-form>
            
                  <v-text-field
                    label="username"
                    name="user"
                    prepend-icon="person"
                    type="text"
                   
                   
                  ></v-text-field>
                              <v-spacer></v-spacer>
 

             <video autoplay class="video"></video>
                </v-form>
              </v-card-text>
              
            <v-btn id="snap" @click="Shoot()" href="/JOC">
        <v-icon x-large >camera</v-icon>
       
      </v-btn>
   
            </v-card>
          </v-col>
      
        </v-row>
        
    </v-content>
  </v-app>
</template>

<script>


export default {
  name: 'FaceID',

  components: {
 
 
  },

  data: () => ({
    imageCapture:null,
    switch1: true,
    title:'LOGIN', 
   
  }),
  methods:{
     inici(){

       
          if('mediaDevices' in navigator && 'getUserMedia' in navigator.mediaDevices){
              
              navigator.mediaDevices.getUserMedia({video:true}).then(mediaStream =>{
               
                 const videoPlayer = document.querySelector("video");
                  videoPlayer.srcObject = mediaStream;
              let mediaStreamTrack = mediaStream.getVideoTracks()[0];
             this.imageCapture = new ImageCapture(mediaStreamTrack);


              }).catch(err=>{
                  console.log("ERROR amb la camèra: ",err);
                  
              })
          }
      }, 
      Shoot(){



console.log(this)
//     this.imageCapture.takePhoto().then( async blob => {
//     const videoPlayer = document.querySelector("video");
//   videoPlayer.src = blob;
// console.log()




//     }).catch((err)=>{
//         console.log('error amb la cptura de la foto', err)
//     });
      }
  },
    beforeMount(){
      this.inici();
  }
};
</script>
<style scoped>
#FaceID{
background: linear-gradient(to right, rgba(188, 250, 231, 0.575),rgba(133, 149, 240, 0.7));
}
#snap{
    width: 75px;
    height: 75px;
    border-radius: 50%;
    outline: none;
    position: relative;
margin-bottom: 10px;
    cursor: pointer;
   background-color: gray;
   margin-left: 45%;
    }
#MenuBar{
background: linear-gradient(to right, rgba(55,236,186,.7),rgba(25,32,72,.7));
}
#login{
  margin-top: 8%;
}
#toolbar{
background: linear-gradient(to right, rgba(55,236,186,.7),rgba(25,32,72,.7));
}
video{
    position: relative;
    width: 100%;
    height: 100%;
    box-shadow: 1px 6px 12px 6px rgb(0, 0, 0,0.25);

}
a{
  text-decoration: none;
}

</style>