import Vue from 'vue'
import VueRouter from 'vue-router'
import FaceID from '@/components/FaceID.vue'
import GameBoard from '@/components/GameBoard.vue'
Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Auth',
    component: FaceID
  },
  {
    path: '/JOC',
    name: 'home',
    component: GameBoard
  }
]

const router = new VueRouter({
  mode: 'history',
  routes
})
export default router